/* global jQuery */

jQuery(($) => {
	'use strict';
	// Accordions
	$('.accordion-wrapper').each((index, accordion) => {
		$(accordion).attr('id', `accordionWrapper${index}`);

		const contentHeights = [];
		$(accordion)
			.find('.accordion-content--wrapper')
			.each((accIndex, contentWrapper) => {
				contentHeights[accIndex] = $(contentWrapper).outerHeight();
			});

		$(accordion)
			.find('.accordion-content')
			.each((accIndex, content) => {
				$(content).css('max-height', contentHeights[accIndex]);
			});
	});
	$('.accordion-wrapper .accordion-tab').on('click', (event) => {
		const panelParent = $(event.target).parent();
		const panelTab = $(event.target).parent().find('.accordion-tab');
		const panelTarget = panelParent.find('.accordion-content');

		if (panelTarget.attr('aria-hidden') === 'true') {
			$('.accordion-container')
				.find('.accordion-content')
				.attr('aria-hidden', 'true');
			panelTarget.attr('aria-hidden', 'false');

			$('.accordion-tab').attr('aria-expanded', 'false');
			panelTab.attr('aria-expanded', 'true');
		} else {
			$('.accordion-container')
				.find('.accordion-content')
				.attr('aria-hidden', 'true');

			$('.accordion-tab').attr('aria-expanded', 'false');
		}
	});
	$('.accordion-wrapper .accordion-tab .accordion--indicator').on(
		'click',
		(event) => {
			const panelParent = $(event.target).parents('.accordion-wrapper');
			const panelTab = $(event.target).parent('.accordion-tab');
			const panelTarget = panelParent.find('.accordion-content');

			if (panelTarget.attr('aria-hidden') === 'true') {
				$('.accordion-container')
					.find('.accordion-content')
					.attr('aria-hidden', 'true');
				panelTarget.attr('aria-hidden', 'false');

				$('.accordion-tab').attr('aria-expanded', 'false');
				panelTab.attr('aria-expanded', 'true');
			} else {
				$('.accordion-container')
					.find('.accordion-content')
					.attr('aria-hidden', 'true');

				$('.accordion-tab').attr('aria-expanded', 'false');
			}
		}
	);
});
